body {
  box-sizing: border-box;
  padding: 10px;
  padding-top: 60px;
  display: grid;
  place-items: center;
  font-family: sans-serif;
  background: #e2e2e2;
}
.subjects {
  max-width: 900px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}
.today {
  box-sizing: border-box;
  position: absolute;
  top: 0;
  width: 90%;
  display: flex;
  justify-content: space-between;
  margin: auto;
  background: #1e1e1e;
  color: white;
  padding: 10px 25px;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 10px 10px 20px #1e1e1e80;
}
.card {
  box-sizing: border-box;
  background: #e2e2e2;
  color: black;
  border-radius: 15px;
  border: 5px solid #1e1e1e;
  width: 280px;
  height: 100px;
  padding: 10px;
  display: grid;
  place-items: center;
  transition: all 1s;
  box-shadow: 10px 10px 20px #1e1e1e40, -10px -10px 20px#ffffff,
    inset 2px 2px 20px #1e1e1e40;
  animation: fade 0.5s ease-in alternate;
}
@keyframes fade {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
.time {
  color: #545454;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
h2 {
  margin: 0;
}
.type {
  width: 100%;
}

/* dropdown windows */

details {
  position: relative;
}
details summary {
  scale: 1.5;
  position: relative;
  transition: 0.5s;
  cursor: pointer;
  list-style: none;
}
details ul {
  padding: 0;
  position: absolute;
  top: 15px;
  left: -100px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 10px 10px 20px #1e1e1e80;
  background: #fff;
}
details ul li {
  padding: 5px 25px;
  background: #1e1e1e;
  color: #ffffff;
  list-style: none;
  transition: all 0.5s;
  cursor: pointer;
}
details ul li:hover {
  color: #000000;
  background: #00000000;
}

details[open] summary {
  color: #ff0000;
}
